/* styles.css */

/* Base styles for the animated text */
.animated-text {
  font-family: sans-serif;
  font-weight: 800;
  transition: transform 0.5s ease;
}

/* Define the animation when the 'scaled-text' class is applied */
.scaled-text {
  transform: scale(0.8);
}

/* Responsive font size adjustments */
@media (max-width: 767px) {
  .animated-text {
    font-size: 30px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .animated-text {
    font-size: 40px;
  }
}

@media (min-width: 992px) {
  .animated-text {
    font-size: 50px;
  }
}

