/* Create the transparent boxes */
.transparent-box {
  position: absolute;
  width: 200px;
  height: 200px;
  background-color: rgba(0, 0, 0, 0.5);
  border: 1px solid black;
  /* This will randomly position the boxes on the screen */
  top: random(0, 100%);
  left: random(0, 100%);
}


.callout{
  padding: 0.5rem 1.25rem 0.5rem;
