select {
  -webkit-appearance:none;
  -moz-appearance:none;
  -ms-appearance:none;
  appearance:none;
  outline:0;
  box-shadow:none;
  border:0!important;
  background: #b0337c;
  background-image: none;
  flex: 1;
  padding: 0 1.5em;
  color:#fff;
  cursor:pointer;
  font-size: 18px;
  font-family: 'Open Sans', sans-serif;
  border-radius: 15px;
height:30px;
}
select::-ms-expand {
  display: none;
}
.select {
  position: relative;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #b0337c;
  overflow: hidden;
  border-radius: .25em;
}
.select::after {
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #2b2e2e;
  cursor:pointer;
  pointer-events:none;
  transition:.25s all ease;
}
.select:hover::after {
  color: #23b499;
}
.labeloption{
  font-size: 1.2vw;
  animation: breatheAnimation 2s ease-in-out infinite;
}
@keyframes breatheAnimation {
  0% {
    transform: scale(1); /* Original size */
  }
  50% {
    transform: scale(1.05); /* Slightly larger */
  }
  100% {
    transform: scale(1); /* Return to original size */
  }
}
